import { $PropertyType } from 'utility-types'
import {
  computeThumbnailParams,
  default as PageThumbnailComponent,
} from '../grid/PageThumbnailComponent'
import { A4_HEIGHT, A4_WIDTH, BREAKPOINT_SM } from '@/constants/config'
import { absurd, PSPDFKitError, showAlert } from '@pspdfkit/shared'
import { applyOperationsAndReloadDocument } from '@/actions/documentOperations'
import { Button } from '@pspdfkit/ui'
import type { IntlShape } from 'react-intl'
import { defineMessages, injectIntl } from 'react-intl'
import { downloadPdfFromBuffer } from '@/utils/dom'
import { List, Map, Set } from 'immutable'
import { Color, Page, Size } from '@/models'
import { serializeDocumentOperation } from '@/lib/serializers/utils'
import * as React from 'react'
import classNames from 'classnames'
import genericMessages from '../messages'
import type { MoveCursorData } from '../GridViewComponent'
import GridViewComponent from '../GridViewComponent'
import InlineSVGComponent from '../InlineSVGComponent'
import LoadingComponent from '../LoadingComponent'
import ResizeHelperComponent from '../helpers/ResizeHelperComponent'
import type { DocumentOperation } from '@/models/DocumentOperation'
import ToolNodeComponent from '../tools/ToolNodeComponent'
import type { DocumentEditorFooterItems } from '@/models/DocumentEditorFooterItems'
import type { DocumentEditorToolbarItems } from '@/models/DocumentEditorToolbarItems'
import {
  DocumentEditorFittingToolbarButton,
  DocumentEditorOverflowingToolbarButton,
} from './DocumentEditorToolbarButton'
import type { DocumentEditorToolbarItem } from '@/models/DocumentEditorToolbarItem'
import type { DocumentEditorFooterItem } from '@/models/DocumentEditorFooterItem'
import type { CssHack } from './DocumentEditorToolbarButton'
import { invariant } from '@/utils/invariant'
import { Dispatch } from '@/typings/store'
import { IFunction } from '@/typings/utils'
import { Backend } from '@/typings/backend'

declare let PSPDFBridge: any
type Props = {
  pages: List<Page>
  dispatch: Dispatch
  onCancel: () => void
  // TODO see TODO in DocumentEditorModalComponent.
  CSS_HACK: CssHack
  scale: number
  onDialog: IFunction
  frameWindow: Window
  backend: Backend
  intl: IntlShape
  footerItems: DocumentEditorFooterItems
  toolbarItems: DocumentEditorToolbarItems
}

const a4Size = new Size({
  width: A4_WIDTH,
  height: A4_HEIGHT,
})

const MAX_DRAG_PREVIEW_PAGES = 5

const DocumentEditorComponent = (props: Props) => {
  const {
    onCancel,
    pages,
    dispatch,
    backend,
    frameWindow,
    intl: { formatMessage },
    onDialog,
    footerItems: _stateFooterItems,
    toolbarItems: _stateToolbarItems,
  } = props
  const footerItems = React.useMemo(
    () => _stateFooterItems.toJS() as any as DocumentEditorFooterItem[],
    [_stateFooterItems],
  )
  const toolbarItems = React.useMemo(
    () => _stateToolbarItems.toJS() as any as DocumentEditorToolbarItem[],
    [_stateToolbarItems],
  )
  const { styles } = props.CSS_HACK
  const [uwpFilenames, setUwpFilenames] = React.useState<Map<string, string>>(Map())
  // The list of document operations that will be applied when the user clicks
  // 'Save'/'Save As'
  const [operations, setOperations] = React.useState<
    List<DocumentOperation | List<DocumentOperation>>
  >(List())
  // How many times the user has clicked undo (increased if the user presses
  // redo)
  const [undoDepth, setUndoDepth] = React.useState(0)
  // The list of document operations, respecting the amount of times the user
  // has clicked undo (what will be previewed in the document editor)
  const undidOperations = operations.slice(0, operations.size - undoDepth)
  // A previewed list of pages in the document which we generate by doing a
  // 'dry run' of the operations, although we have to do this ourselves because
  // it's not possible to do this via Core
  const editorPages = getEditorPages(props.pages, undidOperations, formatMessage, uwpFilenames)
  // Set of selected page indexes (zero indexed)
  const [selectedIndexes, setSelectedIndexes] = React.useState<Set<number>>(Set())
  const [isCommitting, setIsCommitting] = React.useState(false)
  const [useLargeThumbnails, setUseLargeThumbnails] = React.useState(false)
  const moveDialogRef = React.useRef<HTMLElement | null | undefined>(null)
  const moveDialogInputRef = React.useRef<HTMLElement | null | undefined>(null)
  const [moveDialogShown, setMoveDialogShown] = React.useState(false)
  const [moveDialogInputValue, setMoveDialogInputValue] = React.useState('')
  const syncMoveDialogFocus = React.useCallback(() => {
    const input = moveDialogInputRef.current

    if (input != null) {
      if (moveDialogShown && input.ownerDocument.activeElement !== input) {
        input.focus()
      } else if (!moveDialogShown && input.ownerDocument.activeElement === input) {
        input.blur()
      }
    }
  }, [moveDialogShown])
  const setMoveDialogShownAndNotify = React.useCallback(
    shown => {
      setMoveDialogShown(shown)
      syncMoveDialogFocus()
      onDialog(shown)
    },
    [onDialog, syncMoveDialogFocus],
  )

  React.useEffect(() => {
    // Make sure the input inside move dialog handles focus correctly when opened.
    syncMoveDialogFocus()
  }, [syncMoveDialogFocus])

  const pushOperation = React.useCallback(
    (operationOrOperationList: DocumentOperation | List<DocumentOperation>) => {
      setOperations(undidOperations.push(operationOrOperationList))
      setUndoDepth(0)
    },
    [undidOperations],
  )
  const isAliveRef = React.useRef(true)

  React.useLayoutEffect(
    () => () => {
      if (isAliveRef.current) {
        isAliveRef.current = false
      }
    },
    [],
  )

  const handleAddClick = React.useCallback(() => {
    const firstPage = pages.get(0)
    const size = firstPage ? firstPage.pageSize : a4Size
    const operation: any = {
      type: 'addPage',
      backgroundColor: Color.WHITE,
      pageWidth: size.width,
      pageHeight: size.height,
      rotateBy: 0,
    }

    if (selectedIndexes.size === 1) {
      operation.afterPageIndex = selectedIndexes.first()
    } else {
      operation.beforePageIndex = 0
    }

    pushOperation(operation)
    setSelectedIndexes(selectedIndexes.clear())
  }, [pages, selectedIndexes, pushOperation])
  const handleRemoveClick = React.useCallback(() => {
    pushOperation({
      type: 'removePages',
      pageIndexes: selectedIndexes.toArray(),
    })
    setSelectedIndexes(selectedIndexes.clear())
  }, [pushOperation, selectedIndexes])
  const handleDuplicateClick = React.useCallback(() => {
    pushOperation({
      type: 'duplicatePages',
      pageIndexes: selectedIndexes.toArray(),
    })
    setSelectedIndexes(selectedIndexes.clear())
  }, [pushOperation, selectedIndexes])
  const handleRotateLeftClick = React.useCallback(() => {
    pushOperation({
      type: 'rotatePages',
      pageIndexes: selectedIndexes.toArray(),
      rotateBy: 270,
    })
  }, [pushOperation, selectedIndexes])
  const handleRotateRightClick = React.useCallback(() => {
    pushOperation({
      type: 'rotatePages',
      pageIndexes: selectedIndexes.toArray(),
      rotateBy: 90,
    })
  }, [pushOperation, selectedIndexes])
  const handleMoveClick = React.useCallback(() => {
    setMoveDialogShownAndNotify(!moveDialogShown)
  }, [moveDialogShown, setMoveDialogShownAndNotify])
  const handleMoveDialogInputChange = React.useCallback(
    event => {
      const value = event.target.value
      let newValue = value
      const parsedValue = parseInt(value, 10)

      if (!isNaN(parsedValue)) {
        newValue = Math.min(Math.max(parsedValue, 0), editorPages.size).toString()
      }

      setMoveDialogInputValue(newValue)
    },
    [editorPages.size],
  )
  const moveDialogPageIndex = parseInt(moveDialogInputValue, 10)
  const getIsContiguousSelection = React.useCallback((selection: Set<number>) => {
    const isSingle = selection.size === 1
    let lastIndex

    return (
      isSingle ||
      selection.sort().find(index => {
        // Check if any selected indexes has a gap from the previous one
        let isNonContiguous = false

        if (lastIndex != null) {
          isNonContiguous = index !== lastIndex + 1
        }

        lastIndex = index

        return isNonContiguous
      }) == null
    )
  }, [])
  const canMoveToPageIndex = React.useCallback(
    (pageIndex: number, dragSelection?: Set<number>) => {
      const selection = dragSelection != null ? dragSelection : selectedIndexes
      const isContiguous = getIsContiguousSelection(selection)
      const cantMoveToPageIndex = // We can't move a page after itself
        selection.includes(pageIndex - 1) || // We can't move pages to the beginning if they are already there
        (pageIndex === 0 && isContiguous && selection.includes(0)) || // We can't move pages after a page that they are already after
        (isContiguous && selection.sort().first() === pageIndex)

      return !cantMoveToPageIndex
    },
    [selectedIndexes, getIsContiguousSelection],
  )
  const canMoveToDialogPageIndex =
    !isNaN(moveDialogPageIndex) && canMoveToPageIndex(moveDialogPageIndex)
  const movePagesTo = React.useCallback(
    (pageIndex: number, dragSelection?: Set<number>) => {
      const selection = dragSelection != null ? dragSelection : selectedIndexes
      const afterPageIndex = pageIndex - 1

      pushOperation({
        type: 'movePages',
        pageIndexes: selection.toArray(),
        ...(pageIndex === 0
          ? {
              beforePageIndex: 0,
            }
          : {
              afterPageIndex,
            }),
      })

      let nextSelectedIndexes = Set()
      // If we are moving to the beginning of the document (moveDialogPageIndex
      // === 0), then the next selection can simply start at zero.
      let selectedIndexesStart = 0

      if (pageIndex !== 0) {
        // If we are _not_ moving to the beginning of the document, we initially
        // set the start of the next selection to after the destination page.
        // Then, we need to see how many selected pages come before the
        // destination page. For each page that comes before, we decrement the
        // start of the selection, because once that page is moved, the page at
        // that index will be shifted down.
        selectedIndexesStart = afterPageIndex + 1
        selection.forEach(index => {
          if (index < afterPageIndex) {
            --selectedIndexesStart
          }
        })
      }

      // Now we construct the next selection starting from selectedIndexesStart,
      // having the same number of indexes as the previous selection.
      let selectedIndex = selectedIndexesStart

      selection.forEach(() => {
        nextSelectedIndexes = nextSelectedIndexes.add(selectedIndex)
        ++selectedIndex
      })
      setSelectedIndexes(nextSelectedIndexes)
    },
    [selectedIndexes, pushOperation, setSelectedIndexes],
  )
  const handleMoveDialogFormSubmit = React.useCallback(
    event => {
      // Prevent running form action (redirect)
      event.preventDefault()

      if (!canMoveToDialogPageIndex) {
        return
      }

      movePagesTo(moveDialogPageIndex)
      setMoveDialogShownAndNotify(false)
    },
    [canMoveToDialogPageIndex, moveDialogPageIndex, setMoveDialogShownAndNotify, movePagesTo],
  )
  const handleDocumentEditorClick = React.useCallback(
    event => {
      const target = event.target as HTMLElement

      if (!moveDialogShown || target.classList.contains(styles.moveToolbarButton)) {
        // Ignore click if move dialog is not shown or if Move button was clicked.
        // The Move button has its own click handler.
        return
      }

      const moveDialog = moveDialogRef.current

      invariant(moveDialog != null)

      if (!moveDialog.contains(target)) {
        setMoveDialogShownAndNotify(false)
      }
    },
    [moveDialogShown, setMoveDialogShownAndNotify, styles.moveToolbarButton],
  )
  const handleMoveLeftClick = React.useCallback(() => {
    const operations = selectedIndexes
      .sort()
      .toList()
      .map<DocumentOperation>(index => ({
        type: 'movePages',
        pageIndexes: [index],
        beforePageIndex: index - 1,
      }))

    pushOperation(operations)
    // It's been a while since I wrote this but I'm pretty sure we had to
    // convert to array and back to set again because Set#map() was buggy
    setSelectedIndexes(Set(selectedIndexes.toArray().map(pageIndex => pageIndex - 1)))
  }, [pushOperation, selectedIndexes])
  const handleMoveRightClick = React.useCallback(() => {
    const operations = selectedIndexes
      .sort()
      .toList()
      .map<DocumentOperation>(index => ({
        type: 'movePages',
        pageIndexes: [index],
        afterPageIndex: index + 1,
      }))

    pushOperation(operations)
    setSelectedIndexes(Set(selectedIndexes.toArray().map(pageIndex => pageIndex + 1)))
  }, [pushOperation, selectedIndexes])
  const handleUndoClick = React.useCallback(() => {
    setSelectedIndexes(selectedIndexes.clear())
    setUndoDepth(undoDepth + 1)
  }, [selectedIndexes, undoDepth])
  const handleRedoClick = React.useCallback(() => {
    setSelectedIndexes(selectedIndexes.clear())
    setUndoDepth(undoDepth - 1)
  }, [selectedIndexes, undoDepth])
  const handleImportClick = React.useCallback(async () => {
    const position: {
      afterPageIndex?: number
      beforePageIndex?: number
    } = {}

    if (selectedIndexes.size === 1) {
      position.afterPageIndex = selectedIndexes.first<number>()
    } else {
      position.beforePageIndex = 0
    }

    if (PSPDFKIT_DEFINES.STANDALONE_NATIVE) {
      const uwpDocument = JSON.parse(await PSPDFBridge.openDocumentAsync())

      if (uwpDocument.filename == null) {
        // User cancelled the dialog
        return
      }

      const path: string = uwpDocument.path
      const filename: string = uwpDocument.filename

      setUwpFilenames(uwpFilenames.set(path, filename))
      pushOperation({
        type: 'importDocument',
        treatImportedDocumentAsOnePage: true,
        document: path,
        ...position,
      } as DocumentOperation)
    } else {
      const inputEl = document.createElement('input')

      inputEl.type = 'file'
      inputEl.accept = 'application/pdf'

      inputEl.onclick = event => {
        invariant(event.target instanceof HTMLInputElement)

        event.target.value = ''
      }

      inputEl.onchange = event => {
        invariant(event.target instanceof HTMLInputElement)

        if (event.target.files?.length === 0) {
          return
        }

        let newOperations = undidOperations

        // @ts-expect-error
        for (const file of event.target.files) {
          if (typeof file.name !== 'string' || file.name.length === 0) {
            return
          }

          if (file.type !== 'application/pdf') {
            const message = 'The uploaded file must be a PDF.'

            showAlert(message)

            return
          }

          if (
            editorPages.findIndex(
              editorPage =>
                editorPage.type === 'importedDocument' && editorPage.label === file.name,
            ) !== -1
          ) {
            // Document has already been imported
            return
          }

          newOperations = newOperations.push({
            type: 'importDocument',
            treatImportedDocumentAsOnePage: true,
            // File is a Blob
            document: file,
            ...position,
          } as DocumentOperation)
        }

        setOperations(newOperations)
        setUndoDepth(0)
        setSelectedIndexes(selectedIndexes.clear())
      }

      inputEl.click()
    }
  }, [selectedIndexes, uwpFilenames, pushOperation, undidOperations, editorPages])
  const handleSelectAllClick = React.useCallback(() => {
    setSelectedIndexes(editorPages.keySeq().toSet())
  }, [editorPages, setSelectedIndexes])
  const handleSelectNoneClick = React.useCallback(() => {
    setSelectedIndexes(selectedIndexes.clear())
  }, [selectedIndexes, setSelectedIndexes])
  const handlePageClick = React.useCallback(
    (index: number) => {
      if (!selectedIndexes.has(index)) {
        setSelectedIndexes(selectedIndexes.add(index))
      } else {
        setSelectedIndexes(selectedIndexes.delete(index))
      }
    },
    [selectedIndexes, setSelectedIndexes],
  )
  const handleCancelClick = React.useCallback(() => {
    onCancel()
  }, [onCancel])

  // A dummy `resolve()` callback is passed now, but it can be used to be notified when
  // modifications and reloading are complete.
  // However, keep in mind that when the document is reloaded, interactionMode is set to `null`
  // and the Document Editor UI will disappear.
  const handleSaveClick = React.useCallback(() => {
    setIsCommitting(true)
    dispatch(
      applyOperationsAndReloadDocument(
        undidOperations.flatten().toArray() as DocumentOperation[],
        () => {
          if (isAliveRef.current) {
            setIsCommitting(false)
          }
        },
        e => {
          if (isAliveRef.current) {
            setIsCommitting(false)
          }

          throw e
        },
      ),
    )
  }, [dispatch, undidOperations])

  const handleSaveAsClick = React.useCallback(async () => {
    setIsCommitting(true)

    try {
      const buffer = await backend.exportPDFWithOperations(
        // @ts-expect-error
        undidOperations.flatten().toArray().map(serializeDocumentOperation),
      )

      downloadPdfFromBuffer(buffer, frameWindow)
    } catch (e) {
      throw e
    } finally {
      if (isAliveRef.current) {
        setIsCommitting(false)
      }
    }
  }, [backend, undidOperations, frameWindow])

  const renderPage = (index: number, size: number, maxSize: number, movePreview?: boolean) => {
    const editorPage = editorPages.get(index)

    invariant(editorPage != null)

    let item

    switch (editorPage.type) {
      case 'page': {
        item = (
          <PageThumbnailComponent
            key={`page-${editorPage.label}`}
            page={editorPage.page}
            size={size}
            maxSize={maxSize}
            rotation={editorPage.rotation}
          />
        )
        break
      }

      case 'newPage': {
        const { rotatedWidth, rotatedHeight } = computeThumbnailParams(
          editorPage.size,
          editorPage.rotation,
          size,
          maxSize,
        )

        item = (
          <div
            key={`newPage-${editorPage.label}`}
            className={styles.newPage}
            style={{
              width: rotatedWidth,
              height: rotatedHeight,
            }}
          />
        )
        break
      }

      case 'importedDocument': {
        const { containerWidth, containerHeight } = computeThumbnailParams(a4Size, 0, size, maxSize)

        item = (
          <ImportedDocumentPlaceholder
            width={containerWidth}
            height={containerHeight}
            movePreview={movePreview}
            key={`importedDoc-${editorPage.label}`}
            styles={styles}
            formatMessage={formatMessage}
          />
        )
        break
      }

      default:
        item = <></>
        // @ts-expect-error
        absurd(editorPage.type)
    }

    return {
      item,
      label: editorPage.label,
      props:
        editorPage.type === 'page'
          ? {
              'data-original-page-index': editorPage.page.pageIndex,
            }
          : {},
    }
  }

  const renderDragPreview = (
    selectedIndexes: number[],
    targetIndex: number,
    itemSize: number,
    maxSize: number,
  ) => {
    const renderedPages = List(selectedIndexes)
      .filter(it => it !== targetIndex)
      .sort()
      .push(targetIndex)
      .slice(-MAX_DRAG_PREVIEW_PAGES)
      .map(index => {
        return renderPage(index, itemSize, maxSize, true).item
      })
      .toArray()

    return (
      <MovePreviewComponent
        pages={renderedPages}
        style="straight"
        styles={styles}
        previewCount={selectedIndexes.length}
      />
    )
  }

  const canRemove =
    selectedIndexes.size > 0 && selectedIndexes.size !== editorPages.size && !isCommitting
  const canRotate =
    selectedIndexes.size > 0 &&
    selectedIndexes.find(index => {
      const editorPage = editorPages.get(index)

      invariant(editorPage != null)

      return editorPage.type !== 'page' && editorPage.type !== 'newPage'
    }) === undefined
  const canDuplicate =
    selectedIndexes.size > 0 &&
    selectedIndexes.find(index => {
      const editorPage = editorPages.get(index)

      invariant(editorPage != null)

      return editorPage.type !== 'page'
    }) === undefined
  const canMove =
    !selectedIndexes.isEmpty() && selectedIndexes.size !== editorPages.size && !isCommitting
  const canMoveLeft = !selectedIndexes.isEmpty() && !selectedIndexes.includes(0)
  const canMoveRight = !selectedIndexes.isEmpty() && !selectedIndexes.includes(editorPages.size - 1)
  const canUndo = undoDepth < operations.size
  const canRedo = undoDepth > 0
  const canSelectAll = selectedIndexes.size < editorPages.size && !isCommitting
  const canSelectNone = !selectedIndexes.isEmpty() && !isCommitting
  const docEditorRef = React.useRef<HTMLElement | null | undefined>(null)
  const docEditorFocusedRef = React.useRef(false)

  const canInsert = (selectedPages: number[], target: number) => {
    return canMoveToPageIndex(target, Set(selectedPages))
  }

  React.useLayoutEffect(() => {
    const docEditor = docEditorRef.current

    if (docEditor == null) {
      return
    }

    // When first shown the document editor may not be focused, preventing
    // shortcuts from being recognized
    if (!docEditorFocusedRef.current) {
      docEditor.focus()
      docEditorFocusedRef.current = true
    }

    const handleKeyDown = (event: KeyboardEvent) => {
      if (document.activeElement != null && document.activeElement.tagName === 'INPUT') {
        return
      }

      if (isCommitting) {
        return
      }

      const key = event.key.toLowerCase()
      const metaOrCtrl = event.metaKey || event.ctrlKey
      const metaOrCtrlOnly = metaOrCtrl && !event.shiftKey && !event.altKey
      const altKeyOnly = event.altKey && !metaOrCtrl && !event.shiftKey
      const noModifiers = !metaOrCtrl && !event.shiftKey && !event.altKey

      // Keyboard shortcuts:
      //
      // (N)ew Page
      // (D)elete Pages
      // (C)lone (Duplicate)
      // (M)ove
      // (I)mport Document
      //
      // Alt+Left         = Move Left
      // Alt+Right        = Move Right
      // Alt+Shift+Left   = Rotate Left
      // Alt+Shift+Right  = Rotate Right
      // Ctrl/Cmd+Z       = Undo
      // Ctrl/Cmd+Shift+Z = Redo
      // Ctrl/Cmd+A       = Select All
      // Ctrl/Cmd+D       = Select None
      if (event.altKey && event.shiftKey && !metaOrCtrl && key === 'arrowleft' && canRotate) {
        handleRotateLeftClick()
      } else if (
        event.altKey &&
        event.shiftKey &&
        !metaOrCtrl &&
        key === 'arrowright' &&
        canRotate
      ) {
        handleRotateRightClick()
      } else if (altKeyOnly && key === 'arrowleft' && canMoveLeft) {
        handleMoveLeftClick()
      } else if (altKeyOnly && key === 'arrowright' && canMoveRight) {
        handleMoveRightClick()
      } else if (metaOrCtrl && event.shiftKey && !event.altKey && key === 'z' && canRedo) {
        handleRedoClick()
      } else if (metaOrCtrlOnly && key === 'z' && canUndo) {
        handleUndoClick()
      } else if (metaOrCtrlOnly && key === 'a' && canSelectAll) {
        handleSelectAllClick()
      } else if (metaOrCtrlOnly && key === 'd' && canSelectNone) {
        handleSelectNoneClick()
      } else if (noModifiers && key === 'n') {
        handleAddClick()
      } else if (noModifiers && key === 'd' && canRemove) {
        handleRemoveClick()
      } else if (noModifiers && key === 'c' && canDuplicate) {
        handleDuplicateClick()
      } else if (noModifiers && key === 'l' && canRotate) {
        handleRotateLeftClick()
      } else if (noModifiers && key === 'r' && canRotate) {
        handleRotateRightClick()
      } else if (noModifiers && key === 'm' && canMove) {
        setMoveDialogShownAndNotify(true)
      } else if (noModifiers && key === 'i') {
        handleImportClick()
      } else {
        return
      }

      event.preventDefault()
    }

    docEditor.addEventListener('keydown', handleKeyDown)

    return () => {
      docEditor.removeEventListener('keydown', handleKeyDown)
    }
  }, [
    canDuplicate,
    canMove,
    canMoveLeft,
    canMoveRight,
    canRedo,
    canRemove,
    canRotate,
    canSelectAll,
    canSelectNone,
    canUndo,
    handleAddClick,
    handleDuplicateClick,
    handleImportClick,
    handleMoveLeftClick,
    handleMoveRightClick,
    handleRedoClick,
    handleRemoveClick,
    handleRotateLeftClick,
    handleRotateRightClick,
    handleSelectAllClick,
    handleSelectNoneClick,
    handleUndoClick,
    isCommitting,
    setMoveDialogShownAndNotify,
  ])

  const toolbarButtonClassName = styles.toolbar.toolbarButton
  const builtInToolbarItems = [
    {
      type: 'add',
      onPress: handleAddClick,
      className: toolbarButtonClassName,
      disabled: isCommitting,
      children: formatMessage(messages.newPage),
    },
    {
      type: 'remove',
      onPress: handleRemoveClick,
      className: toolbarButtonClassName,
      disabled: !canRemove,
      children: formatMessage(messages.removePage),
    },
    {
      type: 'duplicate',
      onPress: handleDuplicateClick,
      className: toolbarButtonClassName,
      children: formatMessage(messages.duplicatePage),
      disabled: !canDuplicate || isCommitting,
    },
    {
      type: 'rotate-left',
      onPress: handleRotateLeftClick,
      className: toolbarButtonClassName,
      children: formatMessage(messages.rotatePageLeft),
      disabled: !canRotate || isCommitting,
    },
    {
      type: 'rotate-right',
      onPress: handleRotateRightClick,
      className: toolbarButtonClassName,
      children: formatMessage(messages.rotatePageRight),
      disabled: !canRotate || isCommitting,
    },
    {
      type: 'move',
      onPress: handleMoveClick,
      className: classNames(toolbarButtonClassName, styles.moveToolbarButton),
      children: formatMessage(messages.openMoveDialog),
      disabled: !canMove,
    },
    {
      type: 'move-left',
      onPress: handleMoveLeftClick,
      className: styles.toolbar.toolbarButton,
      children: formatMessage(messages.moveBefore),
      disabled: !canMoveLeft || isCommitting,
    },
    {
      type: 'move-right',
      onPress: handleMoveRightClick,
      className: toolbarButtonClassName,
      children: formatMessage(messages.moveAfter),
      disabled: !canMoveRight || isCommitting,
    },
    {
      type: 'import-document',
      onPress: handleImportClick,
      className: toolbarButtonClassName,
      children: formatMessage(messages.mergeDocument),
      disabled: isCommitting,
    },
    {
      type: 'spacer',
    },
    {
      type: 'undo',
      onPress: handleUndoClick,
      className: toolbarButtonClassName,
      children: formatMessage(genericMessages.undo),
      disabled: !canUndo || isCommitting,
    },
    {
      type: 'redo',
      onPress: handleRedoClick,
      className: toolbarButtonClassName,
      children: formatMessage(genericMessages.redo),
      disabled: !canRedo || isCommitting,
    },
    {
      type: 'select-all',
      onPress: handleSelectAllClick,
      className: toolbarButtonClassName,
      children: formatMessage(messages.selectAll),
      disabled: !canSelectAll,
    },
    {
      type: 'select-none',
      onPress: handleSelectNoneClick,
      className: toolbarButtonClassName,
      children: formatMessage(messages.selectNone),
      disabled: !canSelectNone,
    },
  ]
  const [fittingToolbarItemsIndex, setFittingToolbarItemsIndex] = React.useState(
    Number.POSITIVE_INFINITY,
  )
  // Here we split the toolbarItems in two arrays:
  // 1. The items that fit in the toolbar.
  // 2. The overflowing ones that will be rendered in a dropdown.
  //
  // When they all fit 2. will obviously be an empty array.
  const [fittingToolbarItems, overflowingToolbarItems] = React.useMemo(() => {
    if (fittingToolbarItemsIndex === Number.POSITIVE_INFINITY) {
      return [toolbarItems, []]
    }

    return [
      toolbarItems.slice(0, fittingToolbarItemsIndex),
      toolbarItems
        .slice(fittingToolbarItemsIndex)
        .filter(item => item.type !== 'spacer')
        .map((item, index) => ({
          index,
          item: { ...item, dropdownGroup: 'documentEditor' },
        })),
    ]
  }, [toolbarItems, fittingToolbarItemsIndex])
  const [editorSize, setEditorSize] = React.useState(new Size())
  const onResize = React.useCallback(
    rect => {
      setEditorSize(prevEditorSize => {
        if (prevEditorSize.width !== rect.width) {
          // This could unfortunately lead to losing focus in the move page dialog,
          // causing issues with iPad's auxiliary keyboard bar displayed at the bottom
          // when input has focus and HW keyboard is connected. To fix/workaround this
          // issue, we fit toolbar items only when the horizontal size changed and skip
          // the toolbar items measurement for vertical-only resizes.
          // On horizontal resize we re-render ALL the toolbar items so
          // that in the next useLayoutEffect we can measure how many fit.
          setFittingToolbarItemsIndex(Number.POSITIVE_INFINITY)
        }

        return new Size({
          width: rect.width,
          height: rect.height,
        })
      })
    },
    [setEditorSize, setFittingToolbarItemsIndex],
  )
  const toolbarRef = React.useRef(null)

  // In this LayoutEffect we find the index of the last toolbar item that fits.
  React.useLayoutEffect(() => {
    const toolbar = toolbarRef.current

    if (!toolbar || editorSize.width === 0) {
      return
    }

    // @ts-expect-error
    const items = toolbar.children

    // Bail when the rendered items === fittingToolbarItemsIndex
    // otherwise we'd have an infinite loop.
    if (items.length === fittingToolbarItemsIndex) {
      return
    }

    // Find the index of the first item that overflows.
    // Starts from 44 that is the size of the dropdown button for the overflowing items.
    // Also add padding.
    // @ts-expect-error
    const computedStyles = toolbar.ownerDocument.defaultView.getComputedStyle(toolbar)
    let totalWidth =
      44 +
      (parseInt(computedStyles.getPropertyValue('padding-left')) || 0) +
      (parseInt(computedStyles.getPropertyValue('padding-right')) || 0)
    const index = [].findIndex.call(items, (child, index) => {
      // Ignore spacer size as it just fills in empty space and shrinks as needed.
      if (toolbarItems[index].type === 'spacer') {
        return false
      }

      // @ts-expect-error
      totalWidth += child.clientWidth

      return totalWidth > editorSize.width
    })

    if (index === -1) {
      setFittingToolbarItemsIndex(Number.POSITIVE_INFINITY)
    } else {
      setFittingToolbarItemsIndex(index)
    }

    // @ts-expect-error
    const currentWindow = toolbar.ownerDocument.defaultView

    if (currentWindow.innerWidth >= BREAKPOINT_SM) {
      setUseLargeThumbnails(true)
    } else {
      setUseLargeThumbnails(false)
    }
  }, [editorSize, fittingToolbarItemsIndex, setFittingToolbarItemsIndex, toolbarItems])

  const handleKeyDown = React.useCallback(
    event => {
      if (event.key === 'Escape' && moveDialogShown) {
        setMoveDialogShownAndNotify(false)
        event.stopPropagation()
      }
    },
    [moveDialogShown, setMoveDialogShownAndNotify],
  )
  const moveToolbarButtonFits = React.useMemo(() => {
    return fittingToolbarItems.find(item => item.type === 'move') != null
  }, [fittingToolbarItems])
  const moveDialog = (
    <div
      className={classNames(
        styles.moveDialog,
        {
          [styles.moveDialogShown]: moveDialogShown,
          [styles.moveDialogDetached]: !moveToolbarButtonFits,
        },
        'PSPDFKit-DocumentEditor-MoveDialog',
      )}
      ref={moveDialogRef as any}
    >
      <form onSubmit={handleMoveDialogFormSubmit} className={styles.moveDialogForm}>
        <span className={styles.moveDialogFormLabel}>
          {formatMessage(messages.insertAfterPage)}
        </span>

        <input
          className={styles.moveDialogFormInput}
          type="number"
          min="0"
          max={editorPages.size}
          value={moveDialogInputValue}
          onChange={handleMoveDialogInputChange}
          ref={moveDialogInputRef as any}
        />
        <Button
          type="submit"
          className={styles.moveDialogMoveButton}
          disabled={!canMoveToDialogPageIndex}
        >
          {formatMessage(messages.move)}
        </Button>
      </form>

      <div className={styles.moveDialogHint}>
        <p className={styles.moveDialogHintText}>
          {formatMessage(messages.docEditorMoveBeginningHint)}
        </p>
      </div>
    </div>
  )
  const handleMovePages = React.useCallback(
    (oldIndexes: number[], newIndex: number) => {
      const dragSelection = Set(oldIndexes)

      if (!canMoveToPageIndex(newIndex, dragSelection)) {
        return
      }

      movePagesTo(newIndex, dragSelection)
    },
    [movePagesTo, canMoveToPageIndex],
  )
  let moveCursor: MoveCursorData | null | undefined
  const renderMoveDialogCursor = moveDialogShown && !isNaN(moveDialogPageIndex)

  if (renderMoveDialogCursor) {
    const renderedPages = selectedIndexes
      .toList()
      .sort()
      .map(index => {
        return renderPage(index, 160, 160, true).item
      })
      .toArray()
    const movePreview = (
      <MovePreviewComponent pages={renderedPages} style="straight" styles={styles} />
    )

    if (renderMoveDialogCursor) {
      if (moveDialogPageIndex === 0) {
        moveCursor = {
          previewContent: movePreview,
          pageIndex: 0,
          position: 'left',
        }
      } else {
        moveCursor = {
          previewContent: movePreview,
          pageIndex: moveDialogPageIndex - 1,
          position: 'right',
        }
      }

      if (!canMoveToDialogPageIndex) {
        ;(moveCursor as MoveCursorData).disabled = true
      }
    }
  }

  const draggingEnabled = !moveDialogShown
  const footerItemsBuiltIn = React.useMemo<
    Record<
      string,
      {
        element: React.ReactElement<any>
        onPress?: IFunction
        disabled?: boolean
        hide?: boolean
        className?: string
      }
    >
  >(() => {
    return {
      cancel: {
        element: <Button>{formatMessage(genericMessages.cancel)}</Button>,
        onPress: handleCancelClick,
      },
      'selected-pages': {
        element: (
          <div>
            <div className={styles.pagesSelectedIcon}>
              <InlineSVGComponent src={require('../../styles/images/editor/multiplePages.svg')} />
            </div>

            {formatMessage(messages.pagesSelected, {
              arg0: selectedIndexes.size,
            })}
          </div>
        ),
        className: classNames({
          [styles.pagesSelectedIndicator]: true,
          [styles.pagesSelectedIndicatorShown]: selectedIndexes.size > 0,
        }),
      },
      spacer: {
        element: <div />,
        className: styles.spacer,
      },
      'loading-indicator': {
        element: <LoadingComponent />,
        hide: !isCommitting,
      },
      'save-as': {
        element: <Button>{formatMessage(genericMessages.saveAs)}</Button>,
        onPress: handleSaveAsClick,
        disabled: isCommitting,
      },
      save: {
        element: <Button primary={true}>{formatMessage(genericMessages.save)}</Button>,
        disabled: undidOperations.isEmpty() || isCommitting,
        onPress: handleSaveClick,
      },
    }
  }, [
    styles,
    formatMessage,
    handleCancelClick,
    handleSaveAsClick,
    handleSaveClick,
    isCommitting,
    undidOperations,
    messages,
    selectedIndexes,
  ])
  const footerComponent = React.useMemo(() => {
    return footerItems.map((footerItem, i) => {
      const { onPress, className, type, node, id } = footerItem

      invariant(type)

      if (type === 'custom') {
        return node ? (
          <ToolNodeComponent
            className={className}
            onPress={onPress ? e => onPress(e, id) : undefined}
            key={id || i}
            node={node}
          />
        ) : null
      } else {
        const builtInFooterItem = footerItemsBuiltIn[type]

        return builtInFooterItem.hide
          ? null
          : React.cloneElement(builtInFooterItem.element, {
              onClick: e => {
                builtInFooterItem.onPress && builtInFooterItem.onPress(e)
              },
              key: type,
              disabled: builtInFooterItem.disabled,
              className: classNames(builtInFooterItem.className, className),
            })
      }
    })
  }, [footerItems, footerItemsBuiltIn])

  return (
    <div
      className={classNames(styles.docEditor, 'PSPDFKit-DocumentEditor')}
      onClick={handleDocumentEditorClick}
      onKeyDown={handleKeyDown}
      tabIndex={'-1' as any}
      ref={docEditorRef as any}
    >
      <div
        className={classNames(
          styles.toolbar.root,
          styles.toolbarRoot,
          'PSPDFKit-DocumentEditor-Toolbar',
        )}
        style={{
          flex: 0,
        }}
      >
        <div ref={toolbarRef} className={styles.toolbarContainer}>
          <DocumentEditorFittingToolbarButton
            items={fittingToolbarItems}
            builtInItems={builtInToolbarItems as any}
            moveDialog={moveDialog}
            CSS_HACK={props.CSS_HACK}
          />
        </div>
        <DocumentEditorOverflowingToolbarButton
          builtInItems={builtInToolbarItems as any}
          items={overflowingToolbarItems}
          CSS_HACK={props.CSS_HACK}
          frameWindow={frameWindow}
        />
      </div>

      <div className={styles.pagesView}>
        <ResizeHelperComponent onResize={onResize} />
        {!moveToolbarButtonFits && moveDialog}
        <div
          className={classNames(styles.pagesGrid, {
            [styles.pagesGridLargeThumbnails]: useLargeThumbnails,
          })}
        >
          <GridViewComponent
            canInsert={canInsert}
            totalItems={editorPages.size}
            width={editorSize.width}
            height={editorSize.height}
            itemScale={props.scale}
            renderItemCallback={renderPage}
            renderDragPreviewCallback={renderDragPreview}
            onItemPress={handlePageClick}
            selectedItemIndexes={selectedIndexes}
            cssPrefix="PSPDFKit-DocumentEditor"
            moveCursor={moveCursor != null ? moveCursor : undefined}
            onItemsMove={draggingEnabled ? handleMovePages : undefined}
          />
        </div>
      </div>

      <div className={classNames(styles.bottomBar, 'PSPDFKit-DocumentEditor-Footer')}>
        {footerComponent}
      </div>
    </div>
  )
}

type ImportedDocumentPlaceholderProps = {
  styles: any
  width: number
  height: number
  movePreview?: boolean
  formatMessage: $PropertyType<IntlShape, 'formatMessage'>
}

const ImportedDocumentPlaceholder = (props: ImportedDocumentPlaceholderProps) => {
  const { styles, formatMessage, movePreview } = props

  return (
    <div
      style={{
        width: props.width,
        height: props.height,
      }}
      className={classNames(styles.importedDocument, {
        [styles.importedDocumentMovePreview]: movePreview,
      })}
    >
      <div className={styles.importedDocumentIconCircle}>
        <InlineSVGComponent src={require('../../styles/images/editor/importDocument.svg')} />
      </div>

      <span className={styles.importedDocumentInfo}>
        {formatMessage(messages.documentMergedHere)}
      </span>
    </div>
  )
}

type Rotation = 0 | 90 | 180 | 270
type EditorPage =
  | {
      type: 'page'
      page: Page
      label: string
      rotation: Rotation
      dupeOf?: string
      dupeNumber?: number
    }
  | {
      type: 'newPage'
      label: string
      rotation: Rotation
      size: Size
    }
  | {
      type: 'importedDocument'
      label: string
    }

const getEditorPages = (
  pages: List<Page>,
  operations: List<DocumentOperation | List<DocumentOperation>>,
  formatMessage: $PropertyType<IntlShape, 'formatMessage'>,
  uwpFilenames: Map<string, string>,
): List<EditorPage> => {
  const flattenedOperations = operations.flatten() as List<DocumentOperation>
  let editorPages: List<EditorPage> = pages.map(
    (page: Page): EditorPage => ({
      type: 'page',
      page: page,
      rotation: 0,
      label: page.pageLabel,
    }),
  )

  const getDupeNumber = (editorPage: EditorPage): number => {
    const originalLabel = editorPage['dupeOf'] || editorPage.label
    let highestDupeNumber = 0

    for (const editorPage of editorPages) {
      if (
        'dupeOf' in editorPage &&
        editorPage.dupeOf != null &&
        editorPage.dupeOf === originalLabel &&
        editorPage.dupeNumber != null &&
        editorPage.dupeNumber > highestDupeNumber
      ) {
        highestDupeNumber = editorPage.dupeNumber
      }
    }

    return highestDupeNumber + 1
  }

  let newPageCount = 0

  for (const operation of flattenedOperations) {
    switch (operation.type) {
      case 'addPage': {
        let editorIndex

        if (operation['afterPageIndex'] != null) {
          editorIndex = operation['afterPageIndex'] + 1
        } else {
          invariant(operation['beforePageIndex'] != null)
          editorIndex = operation['beforePageIndex']
        }

        ++newPageCount
        editorPages = editorPages.insert(editorIndex, {
          type: 'newPage',
          // TODO(marco): Include count as part of localized string
          // We need to give each new page a unique label since we use them as
          // keys
          label: `${formatMessage(messages.newPage)} ${newPageCount}`,
          rotation: 0,
          size: new Size({
            width: operation.pageWidth,
            height: operation.pageHeight,
          }),
        })
        break
      }

      case 'removePages': {
        // Store values ahead of time, since we can't rely on indexes while
        // editorPages is being modified
        const editorPagesToDelete = operation.pageIndexes.map(index => editorPages.get(index))

        for (const editorPage of editorPagesToDelete) {
          invariant(editorPage != null)

          const index = editorPages.indexOf(editorPage)

          editorPages = editorPages.delete(index)
        }

        break
      }

      case 'rotatePages': {
        for (const index of operation.pageIndexes) {
          const editorPage = editorPages.get(index)

          invariant(editorPage != null)

          let newEditorPage
          let newRotation

          invariant(editorPage.type === 'page' || editorPage.type === 'newPage')

          const currentRotation = editorPage.rotation

          // We want to ensure that our rotation can only be 0, 90, 180, or 270,
          // so that our rendering logic is simpler
          if (operation.rotateBy === 90) {
            if (currentRotation === 270) {
              newRotation = 0
            } else {
              newRotation = currentRotation + operation.rotateBy
            }
          } else {
            if (currentRotation === 90) {
              newRotation = 0
            } else if (currentRotation === 180) {
              newRotation = 90
            } else if (currentRotation === 270) {
              newRotation = 180
            } else {
              newRotation = currentRotation + operation.rotateBy
            }
          }

          invariant(
            newRotation === 0 || newRotation === 90 || newRotation === 180 || newRotation === 270,
          )

          if (editorPage.type === 'page') {
            newEditorPage = { ...editorPage, rotation: newRotation }
          } else if (editorPage.type === 'newPage') {
            newEditorPage = { ...editorPage, rotation: newRotation }
          } else {
            throw new PSPDFKitError('Rotation is not allowed on imported documents')
          }

          editorPages = editorPages.set(index, newEditorPage)
        }

        break
      }

      case 'duplicatePages': {
        // We need to store the pages before looping over and modifying editorPages
        const editorPagesToDuplicate = operation.pageIndexes.map(index => editorPages.get(index))

        for (const editorPage of editorPagesToDuplicate) {
          invariant(editorPage != null)
          invariant(editorPage.type === 'page')

          const dupeNumber = getDupeNumber(editorPage)
          const originalLabel = editorPage.dupeOf || editorPage.label
          const index = editorPages.indexOf(editorPage)

          editorPages = editorPages.insert(
            index + 1,
            Object.assign({}, editorPage, {
              label: `${originalLabel} (${dupeNumber})`,
              dupeOf: originalLabel,
              dupeNumber: dupeNumber,
            }),
          )
        }

        break
      }

      case 'movePages': {
        const fromIndexes = operation.pageIndexes

        if (fromIndexes.length === 1) {
          // Moving a single page
          const fromIndex = fromIndexes[0]
          let toIndex

          if (operation['beforePageIndex'] != null) {
            toIndex = operation['beforePageIndex']
          } else {
            invariant(operation['afterPageIndex'] != null)
            toIndex = operation['afterPageIndex'] + 1
          }

          const fromPage = editorPages.get(fromIndex)

          invariant(fromPage != null)

          const toPage = editorPages.get(toIndex)
          const nPages = editorPages.size

          editorPages = editorPages.delete(fromIndex)

          if (toIndex === nPages) {
            editorPages = editorPages.push(fromPage)
          } else if (toIndex === 0) {
            editorPages = editorPages.unshift(fromPage)
          } else {
            invariant(toPage != null)
            editorPages = editorPages.insert(editorPages.indexOf(toPage), fromPage)
          }
        } else {
          // Moving multiple pages
          const sortedFromIndexes = fromIndexes.slice().sort()

          if (operation['beforePageIndex'] != null) {
            // We only use beforePageIndex when moving to the beginning of the
            // document.
            invariant(operation['beforePageIndex'] === 0)

            // These should be reversed now because pushing each one to the
            // beginning will result in them being reversed again.
            const fromPages = sortedFromIndexes.map(index => editorPages.get(index)).reverse()

            for (const page of fromPages) {
              invariant(page != null)
              // Move the page to the beginning of the document
              editorPages = editorPages.delete(editorPages.indexOf(page))
              editorPages = editorPages.unshift(page)
            }
          } else {
            // Moving pages after a certain page in the document
            invariant(operation['afterPageIndex'] != null)

            const afterPage = editorPages.get(operation['afterPageIndex'])

            invariant(afterPage != null)

            const fromPages = sortedFromIndexes.map(index => editorPages.get(index)).reverse()

            for (const page of fromPages) {
              invariant(page != null)
              // Move the page after the destination page. We need to find the
              // page each time because the list changes after every move.
              editorPages = editorPages.delete(editorPages.indexOf(page))
              editorPages = editorPages.insert(editorPages.indexOf(afterPage) + 1, page)
            }
          }
        }

        break
      }

      case 'importDocument': {
        let index

        if ('beforePageIndex' in operation && operation.beforePageIndex != null) {
          index = operation.beforePageIndex
        } else {
          invariant('afterPageIndex' in operation && operation.afterPageIndex != null)
          index = operation.afterPageIndex + 1
        }

        let docName: string

        if (typeof operation.document === 'string') {
          // UWP imported document
          const path: string = operation.document

          invariant(uwpFilenames.has(path))
          // 'Imported Document' should never be used due to the above check
          docName = uwpFilenames.get(path, 'Imported Document')
        } else {
          // @ts-expect-error
          invariant(typeof operation.document.name === 'string')

          // @ts-expect-error
          docName = operation.document.name
        }

        editorPages = editorPages.insert(index, {
          type: 'importedDocument',
          label: docName,
        })
        break
      }

      case 'keepPages':
        throw Error('Unknown document operation')

      case 'applyInstantJson':
        throw Error('Unknown document operation')

      case 'applyXfdf':
        throw Error('Unknown document operation')

      case 'flattenAnnotations':
        throw Error('Unknown document operation')

      case 'performOcr':
        throw Error('Unknown document operation')

      case 'setPageLabel':
        throw Error('Unknown document operation')

      case 'applyRedactions':
        throw Error('Unknown document operation')

      case 'updateMetadata':
        throw Error('Unknown document operation')

      case 'cropPages':
        throw Error('Unknown document operation')

      default:
        // @ts-expect-error
        absurd(operation.type)
    }
  }

  return editorPages
}

type MovePreviewProps = {
  pages: React.ReactNode[]
  previewCount?: number
  style: 'straight' | 'loose'
  styles: any
}

const MovePreviewComponent = (props: MovePreviewProps) => {
  const { pages, style, styles, previewCount } = props

  return (
    <div className={styles.movePreview}>
      <div
        className={classNames(styles.movePreviewPages, {
          [styles.movePreviewPagesLoose]: style === 'loose',
        })}
      >
        {pages}
      </div>

      <div className={styles.movePreviewCount}>{previewCount ? previewCount : pages.length}</div>
    </div>
  )
}

