import type { DocumentEditorToolbarItem } from '@/models/DocumentEditorToolbarItem'
import * as React from 'react'
import classNames from 'classnames'
import ToolNodeComponent from '../tools/ToolNodeComponent'
import type { GroupedItemType } from '../toolbar/ToolbarComponent'
import { toCamelCase } from '@/utils/string'
import { IObject } from '@/typings/utils'

export const DocumentEditorFittingToolbarButton = React.memo<IProps>(
  // @ts-expect-error
  ({
    items,
    builtInItems,
    moveDialog,
    CSS_HACK: {
      components: { ToolbarButtonComponent },
      styles,
    },
  }) => {
    return items.map((item, index) => {
      const builtInItem = builtInItems.find(b => b.type === item.type)

      if (item.type === 'spacer') {
        return (
          <div
            style={{
              flex: 1,
            }}
            className={item.className}
            key={`spacer-${index}`}
          />
        )
      } else if (item.type === 'move' && builtInItem) {
        return (
          <div key={builtInItem.type} className={styles.moveButtonContainer}>
            <ToolbarButtonComponent
              {...builtInItem}
              icon={require('./../../styles/images/editor/move.svg')}
              className={classNames(builtInItem.className, item.className)}
              onPress={e => {
                builtInItem && builtInItem.onPress && builtInItem.onPress(e)
              }}
            />
            {moveDialog}
          </div>
        )
      } else if (builtInItem) {
        const svgName = toCamelCase(builtInItem.type)

        return (
          <ToolbarButtonComponent
            {...builtInItem}
            key={builtInItem.type || index}
            icon={require(`./../../styles/images/editor/${svgName}.svg`)}
            onPress={e => {
              builtInItem && builtInItem.onPress && builtInItem.onPress(e)
            }}
            className={classNames(builtInItem.className, item.className)}
          />
        )
      } else if (item.type === 'custom' && item.node) {
        const { type, ...rest } = item

        return (
          // @ts-expect-error
          <ToolNodeComponent
            {...rest}
            onPress={e => item.onPress && item.onPress(e, item.id)}
            key={item.id || index}
          />
        )
      } else {
        return (
          <ToolbarButtonComponent
            {...item}
            // @ts-expect-error
            key={(builtInItem && builtInItem.type) || index}
            onPress={e => item.onPress && item.onPress(e, item.id)}
          />
        )
      }
    })
  },
)

export const DocumentEditorOverflowingToolbarButton = React.memo<IOverFlowingProps>(
  // @ts-expect-error
  ({
    items,
    builtInItems,
    CSS_HACK: {
      components: { ToolbarDropdownGroupComponent, ToolbarButtonComponent },
      styles,
    },
    frameWindow,
  }) => {
    const _items = items.map(({ item, index }) => {
      const builtIn = builtInItems.find(b => b.type === item.type)

      return builtIn
        ? {
            index,
            item: {
              ...builtIn,
              className: classNames(builtIn.className, item.className),
              onPress: e => {
                builtIn.onPress && builtIn.onPress(e)
              },
            },
          }
        : {
            item,
            index,
          }
    })

    return (
      _items.length > 0 && (
        <>
          <div
            style={{
              flex: 1,
            }}
            key="spacer-responsive"
          />
          <ToolbarDropdownGroupComponent
            icon={{
              type: 'more',
              size: {
                width: 20,
                height: 20,
              },
            }}
            items={_items}
            discreteDropdown={true}
            caretDirection="down"
            role="menu"
            ItemComponent={({ item, isSelectedItem, state, itemComponentProps }) => {
              const builtIn = !isSelectedItem && builtInItems.find(b => b.type === item.item.type)

              if (isSelectedItem) return null

              const svgName = builtIn && builtIn.type ? toCamelCase(builtIn.type) : ''

              return !item.item.node ? (
                <ToolbarButtonComponent
                  {...item.item}
                  role="menuitem"
                  className={classNames(
                    item.item.className,
                    styles.toolbar.dropdownButton,
                    state === 'Focused' && styles.toolbar['dropdownButton' + state],
                  )}
                  icon={
                    builtIn
                      ? require(`./../../styles/images/editor/${svgName}.svg`)
                      : item.item.icon
                  }
                  itemComponentProps={itemComponentProps}
                />
              ) : (
                <ToolNodeComponent
                  {...item.item}
                  onPress={item.item.onPress ? e => item.item.onPress(e, item.id) : undefined}
                  key={item.item.id || item.index}
                />
              )
            }}
            onSelect={(event: MouseEvent | KeyboardEvent, selectedItem: GroupedItemType) => {
              const { onPress, disabled } = selectedItem.item

              if (disabled) {
                return
              }

              if (onPress) {
                onPress(event)
              }
            }}
            noInitialSelection={true}
            frameWindow={frameWindow}
          />
        </>
      )
    )
  },
)

export type CssHack = {
  components: Record<string, React.ComponentType<any>>
  styles: IObject
}

type IProps = {
  items: DocumentEditorToolbarItem[]
  builtInItems: DocumentEditorToolbarItem[]
  moveDialog: any
  CSS_HACK: CssHack
}
type IOverFlowingProps = {
  items: {
    index: number
    item: DocumentEditorToolbarItem
  }[]
  builtInItems: DocumentEditorToolbarItem[]
  CSS_HACK: CssHack
  frameWindow: Window
}
